@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Schedule{background-color:aquamarine;font-family:Inter, sans-serif;padding:1.5em;display:flex;flex-direction:row;justify-content:space-between}.Schedule .Info{font-size:1.5em;display:flex;flex-direction:column}.Schedule .Info .MainBar .Title{padding-right:0.3em;font-weight:700;display:inline}.Schedule .Info .MainBar .Countdown{font-weight:300;display:inline}.Schedule .Info .InfoBar{padding-top:0.4em;font-size:0.6em}.Schedule .Info .InfoBar .Date{padding-right:0.3em;display:inline}.Schedule .Info .InfoBar .Date::after{content:" •"}.Schedule .Info .InfoBar .Description{display:inline}

